"use client";

import * as React from "react";
import { format } from "date-fns";
import { AdminPageShell } from "@prosfin/ui";
import { AppDataTable } from "@/components/admin/data-table/app-data-table";
import { TableRowActions, type RowAction } from "@/components/admin/data-table/table-row-actions";
import { Badge } from "@prosfin/ui";
import { listLeads, updateLeadStatus } from "@/lib/data/leads";
import type { Lead } from "@/types/admin";
import type { DataTableColumn } from "@/components/admin/data-table/types";

const statusColors: Record<Lead["status"], string> = {
  new: "bg-blue-100 text-blue-800",
  contacted: "bg-yellow-100 text-yellow-800",
  qualified: "bg-green-100 text-green-800",
  meeting_scheduled: "bg-purple-100 text-purple-800",
  converted: "bg-gray-100 text-gray-800",
  lost: "bg-red-100 text-red-800",
};

export default function LeadsPage() {
  const [leads, setLeads] = React.useState<Lead[]>([]);
  const [isLoading, setIsLoading] = React.useState(true);

  React.useEffect(() => {
    listLeads().then((data) => {
      setLeads(data);
      setIsLoading(false);
    });
  }, []);

  const handleStatusChange = async (leadId: string, status: Lead["status"]) => {
    await updateLeadStatus(leadId, status);
    setLeads((prev) =>
      prev.map((lead) => (lead.id === leadId ? { ...lead, status } : lead))
    );
  };

  const rowActions: RowAction<Lead>[] = [
    {
      label: "Mark as Contacted",
      onClick: (row) => handleStatusChange(row.id, "contacted"),
    },
    {
      label: "Change Status",
      onClick: (row) => {
        // TODO: Open status change dialog
        console.log("Change status for", row.id);
      },
    },
    {
      label: "Convert to Client",
      onClick: (row) => {
        // TODO: Open convert dialog
        console.log("Convert", row.id);
      },
    },
    {
      label: "Export",
      onClick: (row) => {
        // TODO: Export row
        console.log("Export", row.id);
      },
    },
  ];

  const columns: DataTableColumn<Lead>[] = [
    {
      id: "name",
      header: "Name",
      accessorKey: "name",
      enableSorting: true,
    },
    {
      id: "company",
      header: "Company",
      accessorKey: "company",
      enableSorting: true,
    },
    {
      id: "email",
      header: "Email",
      accessorKey: "email",
    },
    {
      id: "phone",
      header: "Phone",
      accessorKey: "phone",
    },
    {
      id: "status",
      header: "Status",
      accessorKey: "status",
      cell: ({ row }) => (
        <Badge className={statusColors[row.original.status]}>
          {row.original.status}
        </Badge>
      ),
    },
    {
      id: "source",
      header: "Source",
      accessorKey: "source",
    },
    {
      id: "createdAt",
      header: "Created",
      accessorKey: "createdAt",
      cell: ({ row }) =>
        format(new Date(row.original.createdAt), "MMM dd, yyyy"),
    },
    {
      id: "actions",
      header: "",
      cell: ({ row }) => (
        <TableRowActions row={row.original} actions={rowActions} />
      ),
    },
  ];

  return (
    <AdminPageShell
      title="Leads"
      description="Manage and track your leads"
    >
      <AppDataTable
        columns={columns}
        data={leads}
        isLoading={isLoading}
        searchPlaceholder="Search leads..."
        emptyMessage="No leads found"
      />
    </AdminPageShell>
  );
}
